import {
    OrderModel
} from '../../model/order.js'
const orderModel = new OrderModel()
import {
    UserModel
} from '../../model/user.js'
const userModel = new UserModel()
import anImage from '@/components/an-image/an-image.vue'


export default {
    name: "orderList",
    components:{
        anImage,
    },
    data () {
        return {
            no_pic:getApp().globalData.no_pic,
            status: ["全部", "待付款", "待使用", "已完成", "交易关闭"],
            inppwd: '',
            inppwx: '',
            inpname: '',
            visible1: false,
            internal_payment_sn: '',
            data: [],
            keyShow: false,
            wxPayStatus:false,
            actions:[],
            zfid:'',
        }
    },
    props:{
        orders:{
            type:Object
        }
    },
    /**
     * 组件的方法列表
     */
    methods: {
        // 取消订单
        onclose(e) {
            const id = e.currentTarget.dataset.id
            uni.showModal({
                content: '确定取消订单吗？',
                success: res => {
                    if (res.confirm) {
                        this.close(id)
                    } else if (res.cancel) {
                        console.log('用户点击取消')
                    }
                }
            })
        },
        close(id) {
            if (id) {
                orderModel.cancel({
                    id
                }).then((res) => {
                    if (res.code === 200) {
                        wx.showToast({
                            title: '取消订单成功'
                        })
                        setTimeout(() => {
                            this.triggerEvent('closeOrder', true)
                        }, 1000)
                    } else {
                        uni.showToast({
                            title: '取消订单失败',
                            icon: 'none'
                        })
                    }
                })
            }
        },
        handleOpen1() {
            this.setData({
                visible1: true,
                inppwx: '',
                inppwd: ''
            });
        },

        handleClose1() {
            this.setData({
                visible1: false,
                inppwx: '',
                inppwd: ''
            });
        },
        handleClose2() {
            const pwd = this.data.inppwd
            if (pwd.toString().length < 1) {
                uni.showToast({
                    title: '请填写支付密码',
                    icon: 'none'
                })
                return false
            }
            if (pwd.toString().length < 6) {
                uni.showToast({
                    title: '请输入6位密码',
                    icon: 'none'
                })
                return false
            }
            this.setData({
                visible1: false,
            });
            //创建订单
            this.oncommitOrder()
        },
        onOrder(e) {
            const id = e.currentTarget.dataset.id
            uni.navigateTo({
                url: '/pagesC/order_detail/order_detail?id=' + id,
            })
        },
        // 判断是否设置过密码
        async getStartpwd() {
            const res = await userModel.getstartpwd()
            const data = res.data
            if (data.status === false) {
                // 未设置密码
                uni.showToast({
                    title: '你还未设置密码,请先设置密码！',
                    icon: 'none'
                })
                uni.setStorageSync('pwd', false)
                setTimeout(() => {
                    wx.navigateTo({
                        url: '/pages/editPaw/editPaw'
                    })
                }, 1000)
                return false
            } else {
                uni.setStorageSync('pwd', true)
                return true
            }
        },
        async sumpay(internal_payment_sn) {
            // 判断是否设置过密码
            const MPWD = wx.getStorageSync('MPWD')
            if (MPWD) {
                uni.showModal({
                    title: '你已经开通免密支付',
                    content: '是否继续完成支付',
                    success: res => {
                        if (res.confirm) {
                            // wx.setStorageSync('MPWD', true)
                            this.setData({
                                inppwx: '',
                                inppwd: '',
                                internal_payment_sn
                            });
                            this.oncommitOrder()
                        } else if (res.cancel) {

                        }
                    }
                })
                return false
            } else {
                // 设置密码
                const a = await this.getStartpwd()
                if (a) {
                    this.setData({
                        visible1: true,
                        inppwx: '',
                        inppwd: '',
                        internal_payment_sn
                    });
                    // this.oncommitOrder()
                }
            }

        },
        handleClickpay(e){
            const index = e.currentTarget.dataset.id;
            const internal_payment_sn = this.data.internal_payment_sn
            if (index === '0') {
                this.sumpay(internal_payment_sn)
            } else if (index === '1') {
                this.wxPay(internal_payment_sn)
            }
            this.setData({
                wxPayStatus: false
            })
        },
        // 余额支付、
        onpay(order_id,e) {

            // const internal_payment_sn = e.currentTarget.dataset.id
            // const zfid = e.currentTarget.dataset.zfid
            // this.wxPayStatus = true
            // this.internal_payment_sn = internal_payment_sn
            // this.zfid = zfid
            this.$emit('choice_pay_type',order_id)
        },
        oncommitOrder() {
            this.setData({
                visible1: false,
                inppwx: '',
                inppwd: ''
            });
            const zfid = this.data.zfid
            const internal_payment_sn = this.data.internal_payment_sn
            orderModel.yuonpay({
                order_id:internal_payment_sn,
                internal_payment_sn:zfid
            }).then((res) => {
                if (res.code === 200) {
                    wx.showToast({
                        title: '支付成功！',
                        icon: 'none',
                        duration: 1000
                    })
                    setTimeout(() => {
                        this.triggerEvent('closeOrder', true)
                    }, 1000)
                }
            }).catch((e) => {
                console.log(e)
            })
        },
        handleClickpaycolse(){
            this.setData({
                wxPayStatus: false
            })
        },
        // 微信支付
        wxPay(internal_payment_sn){
            const zfid = this.data.zfid
            const paylist = {
                order_id:internal_payment_sn,
                type: 1,
                internal_payment_sn: zfid
            }
            var that = this
            orderModel.onPay(paylist).then((res) => {
                const data = JSON.parse(res.data)
                var time = new Date().getTime()
                uni.requestPayment({
                    timeStamp: data.timeStamp,
                    nonceStr: data.nonceStr,
                    package: data.package,
                    signType: 'MD5',
                    paySign: data.paySign,
                    success(res) {
                        uni.showToast({
                            title: '支付成功',
                            icon: 'none'
                        })
                        setTimeout(() => {
                            that.triggerEvent('closeOrder', true)
                        }, 1000)
                    },
                    fail(res) {
                        uni.showToast({
                            title: '支付失败！',
                            icon: 'none'
                        })
                    }
                })
            }).catch((e) => {
                uni.showToast({
                    title: '支付失败！',
                    icon: 'none'
                })
            })
        }
    },
    watch:{
        orders:{
            handler(val){
                console.log(val)
               // this.orders = JSON.parse(JSON.stringify(val))
               //  console.log( this.orders)
            }
        }
    }

}
